@import "mixins/function";

.page-header{
    text-align: left; 
    border-bottom: none;
}

.table-create-css td{
    border-top: none !important;
}
.container-fluid .row-fluid button.btn-next{
    float: right; 
    background-color: #690F8E; 
    padding: 3px 28px;
}
.date {
    width:35% !important; 
    display: inline !important;
}
.calendar-button{
    position: relative;
    top: -2px;left: 0;
    padding: 9px 15px !important;
    display: inline-block !important;
    width: auto !important; 
    cursor: pointer;
}
.date{    
    left: -6px;    
    position: relative;    
    border-radius: 0 4px 4px 0 !important;
}
