}

/* hien popup chon team vao css */
.css-create-page .modal-dialog, .css-update-page .modal-dialog{
    width: 400px !important;
}
#frm-add-teams{
    text-align: center;
}
.container-teams{
    width:200px; 
    height:300px;
    background: #6e6e6e;
    float:left;}
.btn-add-team{
    position: relative;
    top:134px;
    margin:0 16px; 
    padding: 5px 15px;
    border: none;
    color: #fff;
    float: left;
    font-weight: bold;
    background: -webkit-linear-gradient(#929292, #000); /* For Safari 5.1 to 6.0 */
    background: -o-linear-gradient(#929292, #000); /* For Opera 11.1 to 12.0 */
    background: -moz-linear-gradient(#929292, #000); /* For Firefox 3.6 to 15 */
    background: linear-gradient(#929292, #000); /* Standard syntax */
}

.modal-header{
    background-color:#3c8dbc; 
    border-bottom: 1px solid #d2d6de !important;
}
.modal-header .modal-title{
    color: #fff ;
    font-weight: bold;
    font-size: 18px;
}
.modal-header button.close span{
    color:#fff;
}

.css-create-page .modal-body, .css-update-page .modal-body{
    font-size: 12px;
    overflow-y: auto;
    height: 400px;
}

.list-team-tree li {
    list-style: none;
}

.list-team-tree ul label {
    width: 100%;
}

.modal-footer{
    border-top: 1px solid #3c8dbc !important;
}
.modal-footer button.create-css{
    color: #000 ; 
    font-weight: bold; 
    background: #e3b019 ; 
    padding: 3px 25px;    
    margin-top: 10px;}
.modal-body ul.list-teams{
    padding: 10px;
    list-style: none;
    text-align: left;
    color: #fff;
    font-weight: bold;
}

.modal-body ul.list-teams li{
    cursor: pointer;
    padding: 3px;
}

.modal-body .remove-team{
    margin-bottom: 10px;
    text-align: right;
}

.modal-body .remove-team img{
    cursor: pointer;
}

.width-300{
    width:325px;
}
.date{
    width: 40%;
}
.error{
    color:red;
    font-size: 13px;
    font-weight: 100;
}
.container-date{
    position: relative;
}
#start_date-error{    
    position: absolute;
    top:43px;
    left:0;
}
#end_date-error{    
    position: absolute;
    top:43px;
    left:0;
}
